import { constants } from "./actions";
import { Utilities } from "../../../utilities/utilities";
import keyBy from "lodash/keyBy";
import sortBy from "lodash/sortBy";
import map from "lodash/map";

const consoleSettingsState = {
  settingsFields: {},
  settingsFieldsList: [],
  consoleSidebarSubItems: []
};

export function settingsData(state = consoleSettingsState, action) {
  switch (action.type) {
  case constants.RECEIVE_CONSOLE_SETTINGS:
    const resBody = Object.assign({}, action.payload);
    const orderedFields = sortBy(resBody, "position");
    const fields = keyBy(orderedFields, "name");

    let subItemsArr = [];
    map(fields, (field) => {
      const label = field.title === "" ?  Utilities.toTitleCase(field.name.replace("-", " ")) : field.title;
      const obj = {
        id: field.name,
        label: label
      }
      subItemsArr.push(obj);
    });

    return Object.assign({}, state, {
      settingsFields: fields,
      settingsFieldsList: map(fields, "name"),
      consoleSidebarSubItems: subItemsArr
    });
  default:
    return state;
  }
}
