import * as React from "react";
import Markdown from "react-remarkable";

const StepMessage = ({ message, handleConfirm, isLoading }) => (
  <div className="StepMessage-wrapper">
    <div className={`markdown-wrapper ${message.level || ""}`}>
      <Markdown>{message.contents}</Markdown>
    </div>
    <div className="u-marginTop--30 flex justifyContent--flexEnd">
      <button className="btn primary" onClick={handleConfirm} disabled={isLoading}>{isLoading ? "Confirming": "Confirm"}</button>
    </div>
  </div>
);

export default StepMessage;
