import "isomorphic-fetch";
import consoleSettingsConfig from "../../../assets/console_settings.json";
import { loadingData } from "../../ui/main/actions";

const apiEndpoint = window.env.API_ENDPOINT;
export const constants = {
  RECEIVE_CONSOLE_SETTINGS: "RECEIVE_CONSOLE_SETTINGS"
};

export function receiveConsoleSettings(fields) {
  return {
    type: constants.RECEIVE_CONSOLE_SETTINGS,
    payload: fields
  };
}

export function getConsoleSettings() {
  return async (dispatch) => {
    // if (!appId) return;
    let response;
    dispatch(loadingData("consoleSettingsFields", true));
    try {
      const url = `${apiEndpoint}/console/settings/live`;
      response = await fetch(url, {
        method: "POST",
        body: JSON.stringify({template: consoleSettingsConfig}),
        headers: {
          "Accept": "application/json",
          "Content-Type": "application/json",
        },
      });
      if (!response.ok) {
        if (response.status === 401) {
          // unauthorized
        }
        throw new Error(`Unexpected response status code ${response.status}`);
      }
      const body = await response.json();
      dispatch(loadingData("consoleSettingsFields", false));
      dispatch(receiveConsoleSettings(body.rendered));
    } catch (error) {
      console.log(error);
      return;
    }
  };
}
