import React from "react";
import autoBind from "react-autobind";

import Loader from "./Loader";

export default class StepBuildingAssets extends React.Component {

  constructor(props) {
    super(props);
    autoBind(this);
  }

  componentDidMount() {
    this.startPoll();
  }

  componentDidUpdate(lastProps) {
    if (this.props.status !== lastProps.status) {
      clearTimeout(this.timeout);
      this.startPoll();
    }
  }

  componentWillUnmount() {
    clearTimeout(this.timeout);
  }

  startPoll() {
    this.timeout = setTimeout(() => this.props.getStep("getStatus"), 1000);
  }

  render() {
    const { status } = this.props;
    return (
      <div className="flex1 flex-column justifyContent--center alignItems--center">
        <Loader size="60" />
        {status.source === "render" ? null :
          <p className="u-fontSizer--larger u-color--tundora u-fontWeight--bold u-marginTop--normal u-textAlign--center">
            {status.type === "json" ?
              JSON.parse(status.detail).status
              :
              status.detail
            }
          </p>
        }
      </div>
    );
  }
}
